import React, { useCallback, useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { useSocket } from './Context/SocketProvider';
import Peer from './Service/Peer';
import {FadeLoader} from 'react-spinners'
import './styles.css';


// Clear chats when user disconnects or click next

function MyApp() {

  const [peerConnection, setPeerConnection] = useState(null)

  const [activeUserCount, setActiveUserCount] = useState(null)
  const [notification, setNotification] = useState(null)

  const [myStream, setMyStream] = useState(null)
  const [peerStream, setPeerStream] = useState(null)

  // When user clicks connect -> true / disconnect -> false
  const [chatActive, setChatActive] = useState(false)

  // User messages (Clear when disconnects or click next)
  const [message, setMessage] = useState("")
  const [messages, setMessages] = useState([])

  const [isMobile, setIsMobile] = useState(false);

  const socket = useSocket();

  useEffect(() => {

    const checkResponsive = () => {
      const contentValue = getComputedStyle(document.body, '::before').content;
      setIsMobile(contentValue === '"mobile"');
    };

    window.addEventListener('resize', checkResponsive);
    checkResponsive(); // Initial check

    return () => window.removeEventListener('resize', checkResponsive);
  }, []);

  useEffect(() => {
    alert("Plea!!")
    playVideoFromCamera()
  }, [])

  useEffect(() => {

    // As user clicks on connect
    if(chatActive) {

      socket.connect()

      const newPeerConnection = new Peer(socket, setPeerStream);

      myStream.getTracks().forEach(track => {
        newPeerConnection.peer.addTrack(track, myStream);
      });

      setPeerConnection(prevPeerConnection => {
        // if(prevPeerConnection) {
        //   prevPeerConnection.cleanup();
        // }
        return newPeerConnection;
      });

    } else {

      socket.disconnect();
      setMessages([]);
      setPeerStream(null);
      setPeerConnection(prevPeerConnection => {
        if(prevPeerConnection) {
          prevPeerConnection.cleanup();
        }
        return null;
      });
      setActiveUserCount(null)
    }

  }, [chatActive, socket, myStream])

  useEffect(() => {

    // On Receving Ice Candidate from peer set in peers IC list
    socket.on("ICE_CANDIDATE", async (ice_candidate) => {
      console.log("IC CAND -> ", ice_candidate)
        await peerConnection.addIceCandidate(ice_candidate)
    })

    socket.on("PEER_DISCONNECTED", async () => {
      console.log("Peer Disconnected")
      
      // setPeerStream(stream => {return null})
      setPeerStream(null)
      const newPeerConnection = new Peer(socket, setPeerStream);


      // TODO: Need to check, sometimes myStream is null
      console.log("If stream is coming ..... ", myStream)
      if(myStream) {
        myStream.getTracks().forEach(track => {
          newPeerConnection.peer.addTrack(track, myStream);
        });
      }
      setMessages([]);
      setPeerConnection(prevPeerConnection => {
        if(prevPeerConnection) {
          prevPeerConnection.cleanup();
        }
        return newPeerConnection;
      });

    })

    // Create an offer and send to another peer
    socket.on("CONNECTION_SUCCESS", async () => {
      console.log("CONN SUCC")
      await peerConnection.createOffer();
    })

    // If Offer received, create the answer and send back to remote peer
    socket.on("OFFER", async (offer) => {
        console.log("Offer ", offer)
        await peerConnection.getAnswer(offer)
    })

    // If Answer received, set Remote Desc
    socket.on("ANSWER", async (answer) => {
      console.log("ansr ", answer)
        await peerConnection.setLocalDescription(answer);
    })

    return () => {
      socket.off("ICE_CANDIDATE")
      socket.off("CONNECTION_SUCCESS")
      socket.off("OFFER")
      socket.off("ANSWER")
    }

  }, [socket, peerConnection, myStream])

  // Basic Events 
  useEffect(() => {

    socket.on("ACTIVE_USER_COUNT", (count) => {
      setActiveUserCount(count)
    })

    socket.on("NOTIFICATION", (notification) => {
      setNotification(notification)
      setTimeout(() => {
        setNotification(null)
      }, 2000)
    })

    socket.on("MSG", (msg) => {
      setMessages(msgs => [...msgs, {from:'stranger', msg}])
    })

    return () => {
      socket.off("ACTIVE_USER_COUNT");
      socket.off("MSG")
      socket.off("NOTIFICATION")
    };

  }, [socket])


  // Initalize the camera and add stream in state
  const playVideoFromCamera = async (peerConnection) => {
    try {
        const constraints = {'video': true, 'audio': true};
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        setMyStream(stream)
    } catch(error) {
        console.error('Error opening video camera.', error);
    }
  }

  const handleNextChat = useCallback(()=> {
    socket.emit("NEXT_CHAT");
  }, [socket])

  const handleSendMessage = useCallback((e) => {
    e.preventDefault();
    if (message === "") return;
    socket.emit("MSG", message)
    setMessages(msgs => [...msgs, {from:'you', msg:message}])
    setMessage("")
  }, [message, socket])

  return (
    <>

      {/* Shows Active Users ( Connected Users on server ) */}
      <div style={{ height: '5vh', width: '100%', textAlign: 'center' }}>
        {activeUserCount && (<p>Users Connected : {activeUserCount}</p>)}
      </div>

      {/* Video Streams BOX */}
      <div style={{display: 'flex', flexDirection:isMobile ? 'column' : 'row', width: 'auto', justifyContent: 'space-around', marginBottom: '20px'}}>

        <div style={{width: '100%', maxWidth: '400px', height: 'auto', display: 'flex', flexDirection: 'column', justifyContent: 'center',alignItems: 'center', padding: '4px'}}>
          {/* User Video Stream */}
          {myStream ? (<ReactPlayer 
              style={{ transform: 'scaleX(-1)', margin:'4px' }}
              url={myStream} 
              playing={true} 
              muted={true}   
              controls={true}
              width="100%"
              height="100%"
          />) : <FadeLoader color="#36d7b7" />}
          <div>Your Video</div>
        </div>

        <div style={{width: '100%', maxWidth: '400px', height: 'auto', display: 'flex', flexDirection: 'column',  justifyContent: 'center',alignItems: 'center', padding: '4px'}}>
          {/* Peer Video Stream */}
          {peerStream ? (<ReactPlayer 
              style={{ transform: 'scaleX(-1)', margin:'4px' }}
              url={peerStream} 
              playing={true} 
              muted={true}  
              controls={true}
              width="100%"
              height="100%"
          />) : chatActive && <FadeLoader color="#36d7b7" />}
          <div>Peer Video</div>
        </div>
      </div>

   
      {/* Shows notification when received from server */}
      <div style={{ height: '5vh', width: '100%', textAlign: 'center' }}>
        {notification && (<p>{notification}</p>)}
      </div>
        
      {/* Connect/Disconnect and Next button */}
      <div style={{marginTop: 'auto', height: '5vh', display: 'flex'}}>
        <button style={{width: '100%'}} onClick={() => setChatActive(!chatActive)}>{chatActive ? 'DISCONNECT': 'CONNECT'}</button>
        <button style={{width: '100%'}} disabled={!chatActive} onClick={() => handleNextChat()}>NEXT</button>
      </div> 

      {/* CHAT BOX */}
      <div>
        {/* Chat Area (Scrollable) */}
        <div>
          <ul style={{height: '170px', overflow:'scroll', border: '1px solid grey', padding: '4px'}}>
            {messages.map((msg_obj, index) => <li key={index}>{msg_obj.from} - {msg_obj.msg}</li>)}
          </ul>
        </div>

        {/* Message Input Box and Send Button */}
        <form style={{marginTop: 'auto', height: '5vh', display: 'flex'}} onSubmit={handleSendMessage}>
          <input 
              disabled={!chatActive}
              style={{flex: 2}}
              value={message}
              type='text'
              placeholder='Message' 
              onChange={(e) => setMessage(e.target.value)} 
            />
          <button type='submit' style={{width: '100%', flex: 1}} disabled={!chatActive}>SEND</button>
        </form>
      </div>

        
    </>
  )
}

export default MyApp